import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ContactsPage } from "../pages/contacts/contacts";
import { ContactProfilePage } from "../pages/contacts/contact-profile/contact-profile";
import { TabsPage } from "../pages/tabs-menu/tabs-menu";

import { TextAnalysisService } from "../services/TextAnalysis.service";


@NgModule({
  bootstrap: [IonicApp],
  declarations: [
    MyApp,
    HomePage,
    TabsPage,
  ],
  entryComponents: [
    MyApp,
    HomePage,
    ContactsPage,
    ContactProfilePage,
    TabsPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/contacts/contact-profile/contact-profile.module.ngfactory#ContactProfilePageModuleNgFactory', name: 'ContactProfilePage', segment: 'contact-profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contacts/contacts.module.ngfactory#ContactsPageModuleNgFactory', name: 'ContactsPage', segment: 'contacts', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  providers: [
    StatusBar,
    SplashScreen,
    TextAnalysisService,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
  ],
})
export class AppModule {
}



